.tittle {
  background-color: MediumSeaGreen;
  position: absolute;
  height: 5%;
  width: 100%;
  text-align: center;
}

.body {
  position: absolute;
  display: flex;
  top: 5%;
  width: 100%;
  height: 95%;
}

.map {
  position: absolute;
  background-color: red;
  width: 67%;
  height: 100%;
}

.option {
  position: absolute;
  z-index: 1;
  width: 33%;
  height: 96.5%;
  left: 67%;
  background-color: white;
}

.menu {
  background-color: MediumSeaGreen;
  position: right;
  height: 30px;
  text-align: center;
}

.menu1 {
  background-color: MediumSeaGreen;
  position: right;
  top: 100%;
  height: 30px;
  text-align: center;
}

.resalt {
  position: absolute;
  z-index: 1;
  width: 100%;
  text-align: center;
  background-color: white;
}

.error {
  position: absolute;
  width: 100%;
  height: 100%;
  text-align: center;
  background-color: white;
}

.hold4 {
  position: absolute;
  padding-top: 4%;
  height: 96%;
  width: 100%;
  border-top: 6px solid MediumSeaGreen;
  overflow: auto;
}

.p {
  border-left: 6px solid MediumSeaGreen;
  background-color: lightgrey;
  border-bottom: 6px solid MediumSeaGreen;
  border-top: 1px solid MediumSeaGreen;
}

.p1 {
  border-left: 6px solid MediumSeaGreen;
  border-right: 6px solid MediumSeaGreen;
  background-color: lightgrey;
  border-bottom: 6px solid MediumSeaGreen;
  border-top: 6px solid MediumSeaGreen;
  width: 500%;
}

